.btnGame {
  position: relative;
  width: 150px;
  height: 150px;
  border: 2px solid black;
  border-radius: 15px;
  z-index: 1;
  transition: transform 1.3s;
  animation: btnGame 1s;
}

@keyframes btnGame {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes zoomIn {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
.btnContainerGame {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: top 0.5s, transform 0.5s;
  transform-origin: center center;
  cursor: pointer;
  border-radius: 13px;
  background-size: contain;
}

.btnContainerGame:hover {
  animation: zoomIn 1.5s;
}

