.burgerContainer {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.burgerUl {
  position: relative;
  margin: 0;
  padding: 0;
  display: flex;
  padding: 40px;
}

.burgerUl .burgerLi {
  position: relative;
  list-style: none;
  width: 120px;
  height: 120px;
  margin: 0 30px;
  transform: rotate(-30deg) skew(25deg);
}

.burgerUl .burgerLi span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  line-height: normal;
  background: teal;
  transition: 0.5s;
  display: flex !important;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 30px !important;
  cursor: pointer;
}

.burgerUl .burgerLi:hover span:nth-child(5) {
  transform: translate(40px, -40px);
  opacity: 1;
}

.burgerUl .burgerLi:hover span:nth-child(4) {
  transform: translate(30px, -30px);
  opacity: 0.8;
}

.burgerUl .burgerLi:hover span:nth-child(3) {
  transform: translate(20px, -20px);
  opacity: 0.6;
}

.burgerUl .burgerLi:hover span:nth-child(2) {
  transform: translate(10px, -10px);
  opacity: 0.4;
}

.burgerUl .burgerLi:hover span:nth-child(1) {
  transform: translate(0, 0);
  opacity: 0.2;
}

