  transform: rotateX(-90deg) rotateY(0deg) rotateZ(0deg);
  transform-origin: top; /* Задаем точку, вокруг которой будет происходить вращение */
  perspective: 500px;
  border-radius: 3%;
  box-shadow: inset 0 5px 30px 1px rgba(0, 0, 0, 0.5);
